.main-events .events,
.main-events .resources,
.main-events .tips {
  width: inherit;
  width: 100%;
  margin-bottom: 90px; }

.main-events .events > div:first-child,
.main-events .resources > div:first-child,
.main-events .tips:first-child {
  width: 500px;
  padding: 15px; }

.main-events .top_event > div {
  width: 320px;
  background: #fff;
  margin: 20px;
  height: 230px;
  padding: 10px;
  border: 1px solid #efe8ed;
  border-radius: 4px;
  cursor: pointer;
  -webkit-animation: fadein 500ms ease-in-out;
          animation: fadein 500ms ease-in-out; }
  .main-events .top_event > div img {
    width: 100%; }

.main-events .resources .interview_tip div:first-child {
  width: 50%;
  height: 250px;
  background: #fff;
  margin: 20px;
  border: 1px solid #efe8ed; }

.main-events .resources .interview_tip div:last-child {
  width: 40%;
  border: 1px solid #efe8ed;
  height: 250px;
  background: #fff;
  margin: 20px; }

.main-events .tips .top_tip div {
  width: 25%;
  height: 250px;
  border: 1px solid #efe8ed;
  background: #fff;
  margin: 20px; }

@media (max-width: 414px) {
  .main-events .events > div:first-child,
  .main-events .resources > div:first-child,
  .main-events .tips:first-child {
    width: 90%;
    margin: 0; }
  .main-events .resources .interview_tip div:first-child, .main-events .resources .interview_tip div:last-child {
    width: 95%; }
  .main-events .tips .top_tip div {
    width: 95%; } }



