.join .signup {
  opacity: 0;
  background: url("../../images/sherman.jpg") no-repeat center;
  /* Photo by Sherman Yang on Unsplash */
  -webkit-animation: sherman 500ms ease-in-out 500ms forwards;
          animation: sherman 500ms ease-in-out 500ms forwards; }

.join .landing_sidebar {
  width: 40%;
  height: 100vh;
  background-size: cover;
  border: none;
  border-radius: 4px;
  padding: 20px 30px;
  margin: 0 0 0 30px;
  position: relative; }
  .join .landing_sidebar h3 {
