@-webkit-keyframes sherman {
  0% {
    -webkit-transform: translate(-500px, 1200px);
            transform: translate(-500px, 1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@keyframes sherman {
  0% {
    -webkit-transform: translate(-500px, 1200px);
            transform: translate(-500px, 1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@-webkit-keyframes form {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes form {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes join_info {
  0% {
    opacity: 0;
    -webkit-transform: translate(1200px);
            transform: translate(1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@keyframes join_info {
  0% {
    opacity: 0;
    -webkit-transform: translate(1200px);
            transform: translate(1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@-webkit-keyframes login {
  0% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; }
  50% {
    -webkit-box-shadow: 5px 0px 13px 8px #efe8ed;
            box-shadow: 5px 0px 13px 8px #efe8ed; }
  100% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; } }

@keyframes login {
  0% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; }
  50% {
    -webkit-box-shadow: 5px 0px 13px 8px #efe8ed;
            box-shadow: 5px 0px 13px 8px #efe8ed; }
  100% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; } }

@media (max-width: 414px) {
  .join {
    padding: 0;
    margin: 0; }
    .join .signup {
      display: none; }
    .join .main-center {
      width: 100%;
      padding: 0;
      margin: 0; }
      .join .main-center p {
        text-align: center; }
    .join .credentials_btn {
      -webkit-animation: join_info 500ms ease-in-out 0.3s forwards;
              animation: join_info 500ms ease-in-out 0.3s forwards; } }



