.login {
  margin: 0;
  padding: 0; }
  .login .signin {
    opacity: 0;
    background: url("../../images/stephen-cook.jpg") no-repeat center;
    /* Photo by Sherman Yang on Unsplash */
    -webkit-animation: sherman 500ms ease-in-out 500ms forwards;
            animation: sherman 500ms ease-in-out 500ms forwards; }
  .login .landing_sidebar {
    width: 40%;
    height: 100vh;
    background-size: cover;
    border: none;
    border-radius: 4px;
    padding: 20px 30px;
    margin: 0 0 0 30px;
    position: relative; }
    .login .landing_sidebar h3 {
      color: #fff;
      line-height: 1.35;
      font-size: 24px;
      font-weight: 400; }
    .login .landing_sidebar h1 {
      color: #fff;
      font-size: 48px;
      line-height: 1; }
  .login .landing_sidebar::after {
    opacity: .5; }
  .login .landing_content {
    width: 70%;
    height: 100vh;
    background: #fff;
    padding: 0px 8% 20px;
    -webkit-animation: form 500ms ease-in-out forwards;
            animation: form 500ms ease-in-out forwards; }
    .login .landing_content form {
      margin-bottom: 40px;
      width: 90%; }
    .login .landing_content .btn {
      -webkit-animation: login 1.5s ease-in-out infinite;
              animation: login 1.5s ease-in-out infinite; }
  .login .credentials_btn {
    position: absolute;
    right: 30px;
    top: 20px;
    opacity: 0;
    z-index: 20;
    -webkit-animation: join_info 500ms ease-in-out 1s forwards;
            animation: join_info 500ms ease-in-out 1s forwards; }
    .login .credentials_btn button {
      color: #555;
      padding: 7px;
      border-radius: 4px;
      border: 1px solid #efe8ed;
      margin-left: 30px;
      width: 90px;
      outline: none;
      cursor: pointer; }
    .login .credentials_btn .active {
      background: #999999;
      color: #fff; }

@-webkit-keyframes sherman {
  0% {
    -webkit-transform: translate(-500px, 1200px);
            transform: translate(-500px, 1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@keyframes sherman {
  0% {
    -webkit-transform: translate(-500px, 1200px);
            transform: translate(-500px, 1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@-webkit-keyframes form {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes form {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes join_info {
  0% {
    opacity: 0;
    -webkit-transform: translate(1200px);
            transform: translate(1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@keyframes join_info {
  0% {
    opacity: 0;
    -webkit-transform: translate(1200px);
            transform: translate(1200px); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px);
            transform: translate(0px); } }

@-webkit-keyframes login {
  0% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; }
  50% {
    -webkit-box-shadow: 5px 0px 13px 8px #efe8ed;
            box-shadow: 5px 0px 13px 8px #efe8ed; }
  100% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; } }

@keyframes login {
  0% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; }
  50% {
    -webkit-box-shadow: 5px 0px 13px 8px #efe8ed;
            box-shadow: 5px 0px 13px 8px #efe8ed; }
  100% {
    -webkit-box-shadow: 5px 5px 3px #efe8ed;
            box-shadow: 5px 5px 3px #efe8ed; } }

@media (max-width: 414px) {
  .login {
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0; }
    .login .signin {
      display: none; }
    .login .credentials_btn {
      -webkit-animation: join_info 500ms ease-in-out 0.3s forwards;
              animation: join_info 500ms ease-in-out 0.3s forwards; } }



